import InputArea from "../../plugin/components/InputArea";

export default {
  name: "hot_update",
  components: {
    InputArea
  },
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dialogTitle: "",
      formLabelWidth: "100px",
      dialogFormVisible: false,
      dialogVisible: false,
      tableData: [],
      form: {
        versionUpd: { radio: "*" },
        versionUpdCustom: "",
        channelUpd: { radio: "*" },
        channelUpdCustom: "",
        forbidUpdVersion: { radio: "*" },
        forbidUpdVersionCustom: "",
        forbidUpdChannel: { radio: "*" },
        forbidUpdChannelCustom: "",
        isPublic: { radio: "*" },
        isPublicCustom: "",
        updStatus: { radio: "1" },
        checkList: ["1", "2", "3"],
        updateTime: "",
        version: "",
        update_type: "",
        update_way: "",
        androidConfig: {},
        iosConfig: {},
        windowsConfig: {},
        game_info: ""
      },
      fileData: {
        file: {},
        index: 0,
        count: 0,
        total_size: 0,
        file_name: ""
      },
      formData: {},
      imageUrl: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      loading: false
    };
  },
  filters: {
    formatPlatform: function(val) {
      let str = "";
      let arr = val.split(",");
      if (arr.length == 1) {
        if (arr[i] == 1) {
          str += "安卓";
        } else if (arr[i] == 2) {
          str += "ios";
        } else {
          str += "windows";
        }
        return str;
      } else {
        for (var i = 0; i < arr.length; i++) {
          if (arr[i] == 1) {
            str += "安卓" + ",";
          } else if (arr[i] == 2) {
            str += "ios" + ",";
          } else {
            str += "windows";
          }
        }
        return str;
      }
    }
  },
  methods: {
    resetForm() {
      this.form = {
        versionUpd: { radio: "*" },
        versionUpdCustom: "",
        channelUpd: { radio: "*" },
        channelUpdCustom: "",
        forbidUpdVersion: { radio: "*" },
        forbidUpdVersionCustom: "",
        forbidUpdChannel: { radio: "*" },
        forbidUpdChannelCustom: "",
        isPublic: { radio: "*" },
        isPublicCustom: "",
        updStatus: { radio: "1" },
        checkList: ["1", "2", "3"],
        updateTime: "",
        version: "",
        update_type: "",
        update_way: ""
      };
    },

    getHotUpdateList() {
      this.$http
        .get("v1/backend/sys-conf/hot-update", {
          params: {
            page: this.currentPage,
            limit: this.pageSize
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          }
        });
    },
    openAddDialog() {
      this.resetForm();
      this.dialogTitle = "添加信息";
      this.dialogFormVisible = true;
    },
    addUpdateConf() {
      let data = {
        allow_version:
          this.form.versionUpd.radio === "*"
            ? this.form.versionUpd.radio
            : this.form.versionUpdCustom,
        allow_channel:
          this.form.channelUpd.radio === "*"
            ? this.form.channelUpd.radio
            : this.form.channelUpdCustom,
        deny_version:
          this.form.forbidUpdVersion.radio === "*"
            ? this.form.forbidUpdVersion.radio
            : this.form.forbidUpdVersionCustom,
        deny_channel:
          this.form.forbidUpdChannel.radio === "*"
            ? this.form.forbidUpdChannel.radio
            : this.form.forbidUpdChannelCustom,
        is_public:
          this.form.isPublic.radio === "*"
            ? this.form.isPublic.radio
            : this.form.isPublicCustom,
        status: Number(this.form.updStatus.radio),
        clients: String(this.form.checkList),
        release_time: this.form.updateTime
          ? parseInt(new Date(Number(this.form.updateTime)).getTime() / 1000)
          : 0,
        version_num: this.form.version,
        is_force: Number(this.form.update_type),
        update_type: Number(this.form.update_way),
        game_info: this.form.game_info
      };
      this.$http.post("v1/backend/sys-conf/hot-update", data).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.dialogFormVisible = false;
          this.getHotUpdateList();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        }
      });
    },
    handleEdit(row) {
      console.log(row);
      this.dialogTitle = "编辑";
      this.dialogFormVisible = true;
      this.form.id = row.id;
      if (row.allow_version === "*") {
        this.form.versionUpd.radio = row.allow_version;
      } else {
        this.form.versionUpd.radio = "custom";
        this.form.versionUpdCustom = row.allow_version;
      }
      if (row.allow_channel === "*") {
        this.form.channelUpd.radio = row.allow_channel;
      } else {
        this.form.channelUpd.radio = "custom";
        this.form.channelUpdCustom = row.allow_version;
      }
      if (row.deny_version === "*") {
        this.form.forbidUpdVersion.radio = row.deny_version;
      } else {
        this.form.forbidUpdVersion.radio = "custom";
        this.form.forbidUpdVersionCustom = row.deny_version;
      }
      if (row.deny_channel === "*") {
        this.form.forbidUpdChannel.radio = row.deny_channel;
      } else {
        this.form.forbidUpdChannel.radio = "custom";
        this.form.forbidUpdChannelCustom = row.deny_channel;
      }
      if (row.is_public === "*") {
        this.form.isPublic.radio = row.is_public;
      } else {
        this.form.isPublic.radio = "custom";
        this.form.isPublicCustom = row.is_public;
      }
      (this.form.updStatus.radio = String(row.status)),
        (this.form.checkList = row.platform.split(",")),
        (this.form.updateTime = row.release_time),
        (this.form.version = row.version),
        (this.form.update_type = String(row.is_force)),
        (this.form.update_way = String(row.update_type));
    },
    handleDelete(row) {
      console.log(row);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .delete("v1/backend/sys-conf/hot-update", {
              params: {
                version_id: row.id
              }
            })
            .then(res => {
              console.log(res);
              if (res.data.code === 200) {
                this.getHotUpdateList();
                this.$message({
                  type: "success",
                  message: res.data.msg
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: res.data.msg
          });
        });
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getHotUpdateList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getHotUpdateList();
    },
    handleAvatarSuccess(res, file) {},
    handleChange(file, fileList) {
      console.log(fileList);
    },
    // currentChunk为上传文件块的索引
    uploadChunk(file, currentChunk) {
      var fileReader = new FileReader(),
        // 上传文件块的大小，可自定义
        chunkSize = 200 * 1024 * 1024,
        // 计算改文件的可分为多少块
        chunks = Math.ceil(file.size / chunkSize);
      console.log(chunks);
      var start = currentChunk * chunkSize;
      var end = start + chunkSize >= file.size ? file.size : start + chunkSize;
      this.fileData.file = file.slice(start, end);
      this.fileData.index = currentChunk;
      this.fileData.count = chunks;
      this.fileData.total_size = file.size;
      this.fileData.file_name = file.name;
    },
    //处理单片文件的上传
    loadNext() {
      var start = currentChunk * chunkSize,
        end = start + chunkSize >= file.size ? file.size : start + chunkSize;

      fileReader.readAsBinaryString(file.slice(start, end));
    },
    beforeUpload(file) {
      console.log(file);
      if (this.form.version == "") {
        this.$message({
          type: "error",
          message: "请先填写更新资源包版本号"
        });
        return false;
      } else {
        let currentChunk = 0;
        this.uploadChunk(file, currentChunk);
      }
    },
    uploadFile(f) {
      console.log(f.file);
      let that = this;
      that.loading = true;
      const fileReader = new FileReader();
      // 文件切割后的回调，this.result为切割的文件块
      fileReader.onload = (function(e) {
        // 用FormData传输文件对象
        let fd = new FormData();
        // 设置文件上传接口的需要的参数
        fd.append("file", that.fileData.file);
        // fd.append("index", that.fileData.index);
        // fd.append("count", that.fileData.count);
        fd.append("version", that.form.version);
        fd.append("total_size", that.fileData.total_size);
        fd.append("file_name", that.fileData.file_name);
        // 设置上传的当前的文件块
        // fd.append("fileObj", new Blob([this.result]));
        that.$http
          .post("v1/backend/sys-conf/hot-update/package", fd)
          .then(res => {
            console.log(res);
            if (res.data.code === 200) {
              that.form.game_info = JSON.stringify(res.data.data.list);
              that.form.androidConfig = that.formatResource(
                res.data.data.list.android
              );
              that.form.iosConfig = that.formatResource(
                res.data.data.list.android
              );
              that.form.windowsConfig = that.formatResource(
                res.data.data.list.android
              );
              that.loading = false;
              that.$message({
                type: "success",
                message: "资源包上传成功"
              });
            } else {
              that.loading = false;
              that.$message({
                type: "error",
                message: "资源包上传失败"
              });
            }
          });
      })();
      // this.$http
      //   .post("v1/backend/sys-conf/hot-update/package", fd)
      //   .then(res => {
      //     console.log(res);
      //   });
      // let formData = new FormData();
      // this.fileList.forEach(item => {
      //   formData.append("filename", item.raw);
      //   formData.append("types", 1);
      // });
      // this.$http.post("v1/backend/upload", formData).then(res => {
      //   if (res.data.code === 1) {
      //     this.imageUrl = res.data.path;
      //   }
      // });
    },
    formatResource(obj) {
      console.log(obj);
      var str = "";
      for (var key in obj) {
        let item = obj[key];
        str += key + "项目配置地址:";
        for (var k in item) {
          // return Object.values(k);
          str += item[k];
        }
        str += "\n";
      }
      return str;
    }
  },
  mounted() {
    this.getHotUpdateList();
    // var obj = {
    //   ODMwZGFm: {
    //     game_code: "ODMwZGFm",
    //     manifest_res: "/MDRjNDhi/Yjc1ZGI3/Users/macbookpro2/",
    //     resources_url: "windows1.0.5"
    //   },
    //   ZDg3MjVi: {
    //     game_code: "ZDg3MjVi",
    //     manifest_res: "/MDRjNDhi/Yjc1ZGI3/Users/macbookpro2/",
    //     resources_url: "windows1.0.5"
    //   }
    // };
    // const res = this.formatResource(obj);
    // console.log(res);
  }
};



